const searchInput = document.querySelector('.search')
const searchList = document.querySelector('.search-dropdown-list')
const searchDrpd = document.querySelector('.search-dropdown')
const closeSearch = document.querySelector('.close-search')

document.addEventListener('DOMContentLoaded', ()=>{
    if(searchInput){
        searchInput.addEventListener('focus', () => {
            closeSearch.classList.remove('no-display')
        });

        searchInput.addEventListener('input', (e)=>{
            searchList.innerHTML = ''
            searchDrpd.classList.remove('no-display')
    
            let xhr = new XMLHttpRequest();
                
            xhr.open('POST', `/get-users?filter=${searchInput.value}`, true);
                
            xhr.setRequestHeader('Content-Type', 'application/x-www-form-urlencoded');
                
            xhr.onload = function() {
                
                let nombres = []
                lista = (this.responseText).split(', ')
    
                lista.forEach(elem => {
                    let nombre = elem.split("'")[1]
                    nombres.push(nombre)
                })
    
                nombres.forEach(n => {
                    if(n){
                        const li = document.createElement('li');
                        const a = document.createElement('a');
                        const img = document.createElement('img');
                        img.src = `/static/fotos_perfil/${n}_prof.jpg`
                        img.classList.add('search-user-img')
                        a.href = `/profile/${n}`;
                        a.classList.add('search-link')
                        a.textContent = n;
                        li.classList.add('search-list__li')
                        
                        li.appendChild(img)
                        li.appendChild(a)
                        searchList.appendChild(li);
                    }else{
                        const li = document.createElement('li');
                        li.textContent = 'Usuario no encontrado'
                        searchList.appendChild(li);
                    }
                })

            }
            xhr.send();   
        })
    }

    if(closeSearch){
        closeSearch.addEventListener('click', () => {
            searchDrpd.classList.add('no-display')
            closeSearch.classList.add('no-display')
            searchInput.value = ''
            
            searchList.innerHTML = ''
        })
    }

    
})