//validations holder
const loginForm = document.querySelector('.login-container__form');
const editProfileForm = document.querySelector('.edit-profile-form');
const editPassForm = document.querySelector('.edit-password-form')

//inputs
const username = document.querySelector('.login-username');
const password = document.querySelector('.login-password');
const submitBtn = document.querySelector('.submit-btn');

const editUsername = document.querySelector('.edit-username');
const editAlias = document.querySelector('.edit-alias');
const editBio = document.querySelector('.edit-bio');

const editOldPassword = document.querySelector('.edit-opassword');
const editNewPassword = document.querySelector('.edit-npassword');
const editRNewPassword = document.querySelector('.edit-rnpassword');

//btn
document.addEventListener('DOMContentLoaded', () => {
    if(loginForm){
        loginForm.addEventListener('submit', () => {
            let xhr = new XMLHttpRequest();
            params = "username=" + username.value + "&password=" + password.value;
            
            xhr.open('POST', '/login', true);
            
            xhr.setRequestHeader('Content-Type', 'application/x-www-form-urlencoded');
            
            xhr.onload = function() {}
            
            xhr.send(params);   
        })
    }
        
    if(editProfileForm){
        editProfileForm.addEventListener('submit', () => {
            let xhr = new XMLHttpRequest();
            params = "alias=" + editUsername.value + "&username=" + editUsername.value + "&bio=" + editBio.value;
            
            xhr.open('POST', '/edit-profile', true);
            
            xhr.setRequestHeader('Content-Type', 'application/x-www-form-urlencoded');
            
            xhr.onload = function() {}
            
            xhr.send(params);
        })
    }

    if(editPassForm){
        editPassForm.addEventListener('submit', () => {
            let xhr = new XMLHttpRequest();
            params = "opassword=" + editOldPassword.value + "&npassword=" + editNewPassword.value + "&rnpassword=" + editRNewPassword.value;
            
            xhr.open('POST', '/edit-password', true);

            xhr.setRequestHeader('Content-Type', 'application/x-www-form-urlencoded');
        
            xhr.onload = function() {}
        
            xhr.send(params);
        
        })
    }
})