//botones
const followersList = document.querySelector('.followers-list')
const followingList = document.querySelector('.following-list')

//partes del modal
const followers = document.querySelector('.followers')
const following = document.querySelector('.following')
const followsModalBody = document.querySelector('.follows-modal__body')

document.addEventListener('DOMContentLoaded', ()=>{
    if(followersList){
        followersList.addEventListener('click', ()=>{
            displayFollowersList()
        })
    }
    
    if(followingList){
        followingList.addEventListener('click', ()=>{
            displayFollowingList()
        })
    }
     
    if(followers){
        followers.addEventListener("click", () =>{
            closeFollowersModal()
        })
    }

    if(following){
        following.addEventListener("click", () =>{
            closeFollowingModal()
        })
    }

    if(followsModalBody){
        followsModalBody.addEventListener("click", (e) => {
            e.stopPropagation();
        })
    }
})

function displayFollowersList(){
    followers.classList.remove('no-display')
    followers.style.display = 'flex'
    document.body.style.overflow = 'hidden'
}

function displayFollowingList(){
    following.classList.remove('no-display')
    following.style.display = 'flex'
    document.body.style.overflow = 'hidden'
}

function closeFollowersModal(){
    followers.classList.add('no-display')
    followers.style.display = 'none'
    document.body.style.overflow = 'visible'
}

function closeFollowingModal(){
    following.classList.add('no-display')
    following.style.display = 'none'
    document.body.style.overflow = 'visible'
}