const openChangePic = document.querySelector('.open-change-pic') 
const changeProfPicBg = document.querySelector('.change-prof-pic--bg')
const changeProfPicBody = document.querySelector('.change-prof-pic--body')
const cancelUpload = document.querySelector('.cancel-upload-prof')
const uploadNewBtn = document.querySelector('.upload-new-prof')
const deleteCurrentBtn = document.querySelector('.remove-current-photo')
const newProfInput = document.querySelector('.new-prof-input')
const changeProfForm = document.querySelector('.change-prof-form')

document.addEventListener('DOMContentLoaded', ()=> {
    if(openChangePic){
        openChangePic.addEventListener('click', (e) => {
            e.preventDefault();
            changeProfPicBg.classList.toggle('no-display')
            changeProfPicBg.style.display = 'flex'
            document.body.style.overflow = "hidden"
        })
    }
    
    if(changeProfPicBg){
        changeProfPicBg.addEventListener('click', closeUploadProfModal)
    }
    
    if(cancelUpload){
        cancelUpload.addEventListener('click', closeUploadProfModal)
    }
    
    if(changeProfPicBody){
        changeProfPicBody.addEventListener('click', (e)=>{
            e.stopPropagation();
        })
    }
    
    if(uploadNewBtn){
        uploadNewBtn.addEventListener('click', () =>{
            newProfInput.click();
        })
    }

    if(deleteCurrentBtn){
        deleteCurrentBtn.addEventListener('click', () => {
            changeProfForm.action = '/edit-profile-pic?delete=1'
            changeProfForm.submit()
        })
    }
    
    if(newProfInput){
        newProfInput.addEventListener('change', ()=> {
            changeProfForm.submit();
        })
    }
})


function closeUploadProfModal(){
    changeProfPicBg.classList.toggle('no-display');
    changeProfPicBg.style.display = 'none';
}