const followBtn = document.querySelector('.follow-btn')

document.addEventListener('DOMContentLoaded', () => {
    if(followBtn){
        followBtn.addEventListener("click", () => {
            if(followBtn.classList.contains('followed')){
                followBtn.classList.remove('followed');
                unfollow(followBtn.dataset.user)
            }else{
                followBtn.classList.add('followed');
                follow(followBtn.dataset.user)
            }
        });
    }

})

function follow(followedUser){
    let xhr = new XMLHttpRequest()

    xhr.open('POST', `/follow?followedUser=${followedUser}`, true);

    xhr.setRequestHeader('Content-Type', 'application/x-www-form-urlencoded');

    xhr.onload = function() {
        location.reload()
    }
    
    xhr.send();

}

function unfollow(unfollowedUser){
    let xhr = new XMLHttpRequest()

    xhr.open('POST', `/unfollow?unfollowedUser=${unfollowedUser}`, true);

    xhr.setRequestHeader('Content-Type', 'application/x-www-form-urlencoded');

    xhr.onload = function() {
        location.reload()
    }
    
    xhr.send();
}