const addPostButton = document.querySelector('.add-post');
const addPostModalBg = document.querySelector('.add-post-modal-div');
const addPostModalBody = document.querySelector('.add-post-modal--body');
const addPostModalHeader = document.querySelector('.add-post-modal--header');
const addPostModal = document.querySelector('.add-post-modal');
const uploadPostButton = document.querySelector('.upload-post-btn');
const uploadPostInput = document.querySelector('.file-selector');
const imageToPost = document.querySelector('.img-post');
const arrowLeft = document.querySelector('.arrow-left');
const next = document.querySelector('.next-post-step');
const post = document.createElement('P');
const postInfoDiv = document.querySelector('.upload-new-post-info')
const uploadPostForm = document.querySelector('.upload-new-post-form')
const caption = document.querySelector('.nice-textarea')

let step = 1;

//listeners
if(addPostButton){
    addPostButton.addEventListener('click', ()=>{
        displayAddPost();
    });
}

if(addPostModal){
    addPostModalBg.addEventListener('click', () =>{
        closeAddPost();
    });
}

if(addPostButton){
    addPostModal.addEventListener('click', (event) =>{
        event.stopPropagation();
    });
}

if(uploadPostButton){   
    uploadPostButton.addEventListener('click',  (e) => {
        e.preventDefault();
        uploadPostInput.click()
    });
}

if(uploadPostInput){
    uploadPostInput.addEventListener('change', (e) =>{
        const [file] = uploadPostInput.files;
        
        if(file){
            manageUpload(file);
            step++;
        }
    })
}

if(arrowLeft){
    arrowLeft.addEventListener('click', ()=>{
        restoreAddPostModal()
    })
}

if(next){
    next.addEventListener('click', () => {
        imageToPost.classList.toggle('full-width')
        imageToPost.classList.toggle('half-post-width')
        addPostModal.style.width = 900;
        addPostModalBody.style.display = 'grid'
        addPostModalBody.style.gridTemplateColumns = "2fr 1.5fr"
        post.classList.add('next-post-step');
        post.textContent = "Postear";
        addPostModalHeader.appendChild(post);
        postInfoDiv.style.display = 'block'
        next.remove()
        step++;
    })
}

if(post){
    post.addEventListener('click', () => {
        uploadPostForm.appendChild(caption)
        
        uploadPostForm.submit()
        
        closeAddPost()       
    })
}

//functions
function displayAddPost(){
    addPostModalBg.classList.toggle('no-display');
    addPostModalBg.classList.toggle('add-post-modal--bg');
    document.body.classList.toggle('no-overflow');
}

function closeAddPost(){
    restoreAddPostModal(exit=true)
    addPostModalBg.classList.toggle('no-display');
    addPostModalBg.classList.toggle('add-post-modal--bg');
    document.body.classList.toggle('no-overflow');
}

function manageUpload(file=null){
    Object.entries(addPostModalBody.children).forEach(child => {
        if(child[0] !== '2'  && child[0] !== '3'){
            file ? child[1].style.display = "none" : child[1].style.display = "block"
        }
    });
    imageToPost.classList.toggle("full-width")
    imageToPost.classList.toggle("no-display")
    arrowLeft.classList.toggle("no-display")
    next.classList.toggle("no-display")
    postInfoDiv.style.display = 'none'
    uploadPostForm.classList.toggle('no-display')

    if(file){
        imageToPost.src = URL.createObjectURL(file);
    }
}

function restoreToPreview(){
    addPostModal.style.width = 560;
    addPostModalBody.style.display = 'flex'
    addPostModalHeader.append(next)    
    postInfoDiv.style.display = 'none'
    imageToPost.classList.toggle('full-width')
    imageToPost.classList.toggle('half-post-width')
    post.remove()
}

function restoreAddPostModal(exit = null){
    if(step === 2){
        if(exit){
            manageUpload();
            step = 1;
        }else{
            manageUpload();
            step--;
        }
    }else if(step === 3){
        
        if(exit){
            restoreToPreview();
            manageUpload();
            step = 1;
        }else{
            restoreToPreview();
            step--;
        }
    }
}