const overlays = document.querySelectorAll('.overlay');
const postModalBgs = document.querySelectorAll('.post-modal-div');
const postModal = document.querySelector('.post-modal');
const images = document.querySelectorAll('.profile__posts__imagetag');
const closes = document.querySelectorAll('.close-modal');
const commentBtns = document.querySelectorAll('.comment-icon')
const viewComments = document.querySelectorAll('.view-comments')
const deletePostIcons = document.querySelectorAll('.delete-post-icon')
const deletePostDrops = document.querySelectorAll('.delete-post-dropdown')
const removePosts = document.querySelectorAll('.remove-post')
const deletePostForm = document.querySelector('.delete-post-form')

document.addEventListener('DOMContentLoaded', ()=>{
    overlays.forEach(overlay => {
        const index = overlay.dataset.index;

        overlay.addEventListener('click', ()=>{
            postModalBgs[index].style.display = "flex"
            document.body.style.overflow = 'hidden'
        })
    });

    commentBtns.forEach(btn => {
        const index = btn.dataset.index;
        btn.addEventListener('click', ()=>{
            postModalBgs[index].style.display = "flex"
            document.body.style.overflow = 'hidden'
        })
    })

    viewComments.forEach(view => {
        const index = view.dataset.index;
        view.addEventListener('click', ()=>{
            postModalBgs[index].style.display = "flex"
            document.body.style.overflow = 'hidden'
        })
    })

    closes.forEach(close => {
        const index = close.dataset.index;
        close.addEventListener('click', ()=>{
            postModalBgs[index].style.display = "none"
            document.body.style.overflow = 'visible'
            
            //iterete dropdowns to close them
        })
    })

    //iterate
    deletePostIcons.forEach(deletePostIcon => {
        deletePostIcon.addEventListener('click', () => {
            deletePostDrops[deletePostIcon.dataset.index].classList.toggle('no-display')
        })
    })
   
})

if(postModal){
    postModal.addEventListener('click', (e) => {
        e.stopPropagation();
    })
}


if(removePosts){
    removePosts.forEach(removePost =>  {
        removePost.addEventListener('click', () => {
            deletePostForm.submit()
        })
    })

}